.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('/src/images/header/fon.jpg');
  background-repeat: no-repeat;
  background-size:cover;
}

.header-container {
  position: relative;
  padding: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 768px) {
    padding: 32px;
  }
}

.nav {
  @media screen and (min-width: 1200px) {
  display: flex;
  width: 100%;
  }  
  margin-bottom: 20px;
}

.menu__header-list{
font-weight: 500;
font-size: 14px;
line-height: 1.14;
letter-spacing: 0.02em;
display: flex;
@media screen and (max-width: 767px) {
    display: none;
  }
@media screen and (min-width: 768px)  {
  // margin-left: 88px;
  margin-top: 30px;
 }
 @media screen and (min-width: 1200px)  {
  margin-left: 10px;
  margin-top: 10px;
 }
}

.menu__header-connect-list{
font-weight: 500;
font-size: 14px;
line-height: 1.14;
letter-spacing: 0.02em;
@media screen and (min-width: 768px){
  display: block;
  font-size: 12px;
}
@media screen and (min-width: 1200px){
display: flex;
font-size: 14px;
}
}

.menu{
display: flex;
align-items: center;
}

.menu__header-item{
  &:not(:last-child){
margin-right: 50px;
  }
}

