.header-connect{
margin-left: auto;
@media screen and(max-width: 767px) {
    display: none;
  }
}

.menu__header-link.current{
color:$text-main-color;
}

.menu__header-link{
color: $text-main-color;
text-shadow:2px 2px 4px #000000;
transition: color 250ms;
transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
position: relative;
outline: none;
&:hover,
&:focus,
.current{
color: #ffffff;
}
}

.menu__header-mail{
  display: flex;
  align-items: center;
  color: $text-main-color;
  transition: color 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
    &:hover,
  &:focus{
  color: #ffffff;
  }
}

.menu__mail-icon{
  margin-right: 10px;
  fill: currentColor;
    &:hover,
  &:focus{
    color: var(--fourth-text-color);
  }
}

.menu__phone-icon{
  margin-right: 10px;
  fill: currentColor;
    &:hover,
  &:focus{
    color: var(--fourth-text-color);
  }
}

.menu__header-tel{
display: flex;
align-items: center;
margin-left: 48px;
color: $text-main-color;
transition: color 250ms;
transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
outline: none;
&:hover,
&:focus{
color: #ffffff
}
  @media screen and (max-width: 1199px){
  margin-top: 10px;
  margin-left: 0px;
  }
}

