.logo{
  font-family: Raleway, sans-serif;
  font-weight: 700;
   @media screen and (max-width: 1199px)  {
  font-size: 24px;
   }
  font-size: 26px;
  line-height: 1.19;
  /* identical to box height */
  letter-spacing: 0.03em;
  color: $text-main-color;
  text-shadow:2px 2px 4px #000000;
  &{
  outline: none;
  }
   @media screen and (min-width: 1200px)  {
    margin-right: 20px;
   }
  }
  
  .logo__dark{
  font-family: Raleway, sans-serif;
  font-weight: 700;
  @media screen and (max-width: 1199px)  {
  font-size: 24px;
   }
  font-size: 26px;
  line-height: 1.19;
  /* identical to box height */
  letter-spacing: 0.03em;
  
  color: #ffffff;
  }
  
  .logo__light{
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 700;
  @media screen and (max-width: 1199px)  {
  font-size: 24px;
   }
  font-size: 26px;
  line-height: 1.19;
  /* identical to box height */
  letter-spacing: 0.03em;
  color:#ffffff;
  }
  