.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

body {
  color: #000;
  font-family: Roboto, sans-serif;
}

body.light-theme {
  color: #000;
}

body.dark-theme {
  color: rgba(255, 255, 255, .9);
  background-color: #121212;
}

body.dark-theme.dark-theme footer {
  background-color: inherit;
}

.body__container {
  max-width: 100%;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:before, :after {
  box-sizing: border-box;
}

a {
