.backdrop {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("fon.4fbb0187.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  transition: opacity 1s, visibility 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-window {
  transform: translate(-50%, -50%) scale(1) rotate(0);
  background-color: #fff;
  background-image: linear-gradient(rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("massage.708fb227.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 40px;
  transition: transform 1s;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 480px) {
  .modal-window {
    width: 450px;
    min-height: 609px;
  }
}

